.videoCard {
    margin-bottom: 40px;
    width: 270px;
}

.videoCard_thumbnail {
    height: 140px;
    width: 250px;
    border-radius: 5px;
}

.videoCard_info {
    display: flex;
    margin-top: 10px;
    padding-right: 30px;
}

.videoCard_text {
    margin-left: 15px;
}

.videoCard_text > h4 {
    font-size: 14px;
    margin-bottom: 5px;
}

.videoCard_text > p {
    font-size: 12px;
    color: gray;
    font-weight: bold;
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.videoCard_avatar {
    height: 30px !important;
    width: 30px !important;
}